import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import io from 'socket.io-client'
import AudioAnalyser from './AudioAnalyser';
import ResizeSensor from 'resize-sensor--react';
import 'resize-sensor--react/build/resize-sensor.css';

import { ReactComponent as ArrowBack } from '../../assets/images/svg/arrow_back_black.svg';
import { ReactComponent as MicOff } from '../../assets/images/svg/mic_off_white.svg';
import { ReactComponent as MicOn } from '../../assets/images/svg/mic_white.svg';
import { ReactComponent as MsgBox } from '../../assets/images/svg/msgBox_white.svg';
import { ReactComponent as Power } from '../../assets/images/svg/power_white.svg';
import { ReactComponent as ScreenShare } from '../../assets/images/svg/screen_share_white.svg';
import { ReactComponent as Send } from '../../assets/images/svg/send_black.svg';
import { ReactComponent as Settings } from '../../assets/images/svg/settings_white.svg';
import { ReactComponent as Speaker } from '../../assets/images/svg/speaker_white.svg';
import { ReactComponent as StopScreenShare } from '../../assets/images/svg/stop_screen_share_white.svg';
import { ReactComponent as VideoOff } from '../../assets/images/svg/videocam_off_white.svg';
import { ReactComponent as VideoOn } from '../../assets/images/svg/videocam_white.svg';

import testAudio from '../../assets/videos/horse.ogv'

const error = console.error;


const server_url = "http://localhost:5000"
const userId = "Arif"

var connections = {}
var victims = {}
const peerConnectionConfig = {
      'iceServers': [
            // { 'urls': 'stun:stun.services.mozilla.com' },
            { 'urls': 'stun:stun.l.google.com:19302' },
      ]
}
var socket = null, socketId = null
var elms = 1



class Video extends Component {
      constructor(props) {
            super(props);
            this.roomId = window.location.pathname.toLowerCase().replace('/room/', '');

            this.localVideoref = React.createRef()
            this.localVideoTestref = React.createRef()
            this.localAudioTestref = React.createRef()

            this.videoAvailable = false
            this.audioAvailable = false
            this.audioTracks = []
            this.videoTracks = []
            this.screenTracks = []
            this.newStream = null
            this.sendMessage = null

            this.state = {
                  userId: userId,
                  userData: {},
                  video: false,
                  audio: false,
                  screen: false,
                  audioInDevices: [],
                  currentAudioInDevice: '',
                  audioOutDevices: [],
                  currentAudioOutDevice: '',
                  videoDevices: [],
                  currentVideoDevice: '',
                  screenAvailable: false,
                  conformation: true,
                  message: "",
                  messages: false,
                  msgArray: [
                        // {
                        //       sender: "Arif",
                        //       message: "Hello World! What's Happinings??",
                        //       time: "12:04 pm"
                        // },
                        // {
                        //       sender: "Arif",
                        //       message: "Hello World! What's Happinings??Hello World! What's Happinings??Hello World! What's Happinings??",
                        //       time: "12:40 pm"
                        // }
                  ],
                  time: '',
                  popup: false,
                  popupScreen: 'menu'
            }

            // this.getPermissions()
      }
      getPermissions = async (e) => {
            try {
                  await navigator.mediaDevices.getUserMedia({ video: true }) // Get Video Permission.
                        .then((stream) => { this.videoAvailable = true; stream.oninactive = () => { console.log("Camera Failed") } })
                        .catch((e) => { this.videoAvailable = false; error("Video Permission Denied\n", e) })

                  await navigator.mediaDevices.getUserMedia({ audio: true }) // Get Audio Permission.
                        .then(() => this.audioAvailable = true)
                        .catch((e) => { this.audioAvailable = false; error("Audio Permission Denied\n", e) })

                  if (navigator.mediaDevices.getDisplayMedia) { // Get ScreenShare Permission.
                        this.setState({ screenAvailable: true })
                  } else {
                        this.setState({ screenAvailable: false })
                        error("ScreenShare Permission Denied")
                  }

                  if (this.videoAvailable || this.audioAvailable) {
                        // Get Devices Info.
                        await navigator.mediaDevices.enumerateDevices()
                              .then(devices => {
                                    this.setState({ videoDevices: devices.filter(device => device.kind === 'videoinput') })
                                    // console.log(this.videoDevices);
                                    this.setState({ audioInDevices: devices.filter(device => device.kind === 'audioinput') })
                                    this.setState({ audioOutDevices: devices.filter(device => device.kind === 'audiooutput') })
                              }).catch(error);
                        // Default Stream.
                        await navigator.mediaDevices.getUserMedia({ video: this.videoAvailable, audio: this.audioAvailable })
                              .then((stream) => {
                                    this.setState({ video: this.videoAvailable, audio: this.audioAvailable })
                                    window.localStream = stream
                                    this.localVideoref.current.srcObject = stream
                                    stream.onended = () => {
                                          console.log("camera removed");
                                    }
                              })
                              .catch(error);
                  }
            } catch (e) { error(e) }
      }

      init = async () => {
            await navigator.mediaDevices.getUserMedia({ video: this.videoAvailable, audio: this.audioAvailable })
                  .then((stream) => {
                        window.localStream = stream
                        this.localVideoref.current.srcObject = stream
                        stream.onended = () => {
                              console.log("camera removed");
                        }
                  })
                  .catch(error);
            if (!this.state.audio) {
                  console.log("init");
                  this.setState({ audio: true }, () => {
                        this.handleAudio()
                  })
            } else {
                  this.setState({ audio: false }, () => {
                        this.handleAudio()
                  })
            }
            if (!this.state.video) {
                  this.setState({ video: true }, () => {
                        this.handleVideo()
                  })
            }
            this.connectTotheSocketServer();
      }

      getNewStream = async (options) => {
            return await navigator.mediaDevices.getUserMedia(options)
                  .then(stream => {
                        this.newStream = stream;
                  })
                  .catch(error)
      }

      createStream = async (Tracks) => {
            let stream = new MediaStream(Tracks);
            window.localStream = stream;
            this.localVideoref.current.srcObject = stream;
            return stream;
      }

      handleAudio = (e) => {
            this.setState({ audio: !this.state.audio }, () => {
                  if (!this.state.audio) {
                        let tracks = window.localStream.getAudioTracks()
                        this.audioTracks = tracks
                        tracks.forEach(track => {
                              window.localStream.removeTrack(track)
                        })
                  } else if (this.audioAvailable) {
                        this.audioTracks.forEach(track => {
                              window.localStream.addTrack(track)
                        })
                  }
            })
      }
      handleVideo = (e) => {
            this.setState({ video: !this.state.video }, () => {
                  if (!this.state.video) {
                        let tracks = window.localStream.getVideoTracks()
                        this.videoTracks = tracks
                        tracks.forEach(track => {
                              window.localStream.removeTrack(track)
                        })
                        this.localVideoref.current.style.opacity = 0;
                  } else if (this.videoAvailable) {
                        this.videoTracks.forEach(track => {
                              window.localStream.addTrack(track)
                        })
                        this.localVideoref.current.style.opacity = 1;
                  }
            })
      }

      screenShare = async (e) => {

            if (!this.state.screen) {
                  if (navigator.mediaDevices.getDisplayMedia) {
                        try {
                              let displayStream = await navigator.mediaDevices.getDisplayMedia({ video: true, audio: true })
                              this.screenTracks = displayStream.getTracks()
                              let videoTracks = window.localStream.getVideoTracks()
                              this.videoTracks = videoTracks
                              videoTracks.forEach(track => {
                                    window.localStream.removeTrack(track);
                              })
                              displayStream.getTracks().forEach(track => {
                                    window.localStream.addTrack(track)
                              })
                              displayStream.oninactive = function (e) {
                                    displayStream.getTracks().forEach(track => {
                                          window.localStream.removeTrack(track)
                                    })
                                    videoTracks.forEach(track => {
                                          window.localStream.addTrack(track);
                                    })
                                    this.setState({ screen: false })
                              }.bind(this)
                              this.setState({ screen: true })
                        } catch (err) {
                              this.screenTracks.forEach(track => {
                                    window.localStream.removeTrack(track)
                                    track.stop();
                              })
                              this.videoTracks.forEach(track => {
                                    window.localStream.addTrack(track);
                              })
                        }
                  }
            } else {
                  this.screenTracks.forEach(track => {
                        window.localStream.removeTrack(track)
                        track.stop();
                  })
                  this.videoTracks.forEach(track => {
                        window.localStream.addTrack(track);
                  })
                  this.setState({ screen: false })
            }
      }

      handleChange = async (e) => {
            if (e.target.name === 'video') {
                  let device = e.target.value
                  this.setState({ currentVideoDevice: device })
                  let option = {
                        video: {
                              deviceId: device
                        }
                  }
                  await this.getNewStream(option)
                  let videoTracks = window.localStream.getVideoTracks()
                  videoTracks.forEach(track => {
                        window.localStream.removeTrack(track);
                  })
                  let newVideoTracks = this.newStream.getVideoTracks()
                  this.videoTracks = newVideoTracks
                  newVideoTracks.forEach(track => {
                        window.localStream.addTrack(track)
                  })
            } else if (e.target.name === 'audioIn') {
                  let device = e.target.value
                  this.setState({ currentAudioInDevice: device })
                  let option = {
                        audio: {
                              deviceId: device
                        }
                  }
                  await this.getNewStream(option)
                  let audioTracks = window.localStream.getAudioTracks()
                  audioTracks.forEach(track => {
                        window.localStream.removeTrack(track);
                  })
                  let newAudioTracks = this.newStream.getAudioTracks()
                  this.audioTracks = newAudioTracks
                  if (this.state.audio) {
                        newAudioTracks.forEach(track => {
                              window.localStream.addTrack(track)
                        })
                  }
            } else if (e.target.name === 'audioOut') {
                  let device = e.target.value
                  this.setState({ currentAudioOutDevice: device })
                  let videos = document.querySelectorAll('video')
                  videos.forEach(elm => {
                        if (typeof elm.sinkId !== 'undefined') {
                              elm.setSinkId(e.target.value)
                                    .then()
                                    .catch(error)
                              this.localAudioTestref.current.setSinkId(e.target.value)
                                    .then()
                                    .catch(error)
                        }
                  })
            }
      }

      audioVisulaizer = async (stream, main) => {
            const audioCtx = new AudioContext();
            const data = new Uint8Array(3 * 4);

            const analyserNode = new AnalyserNode(audioCtx, {
                  fftSize: Math.max(3 * 4, 32),
                  maxDecibels: -20,
                  minDecibels: -80,
                  smoothingTimeConstant: 0.8
            });

            const elNodes = Array.from({ length: 3 }, (n, i) => {
                  let node = document.createElement('div');
                  node.className = 'rounded-full bg-white min-h-1 w-1 inline-block space-x-0.5';
                  main.appendChild(node);
                  return node;
            });

            function updateVisualizer() {
                  requestAnimationFrame(updateVisualizer);
                  analyserNode.getByteFrequencyData(data);
                  elNodes.forEach((node, i) => {
                        node.style.setProperty( 'height',(data[i] / 255) * (1 + (i / 3)) );
                  });
            }

            let tracks = await stream.getAudioTracks()
            if(tracks.length !== 0){
                  audioCtx.createMediaStreamSource(stream);
                  stream.connect(analyserNode);
                  updateVisualizer();
            }
      }

      changeCssVideos = (main) => {
            let widthMain = main.offsetWidth
            let heightMain = main.offsetHeight
            let minWidth = 20
            let minHeight = 20
            if ((widthMain * 25 / 100) < 300) {
                  minWidth = 25
            }
            if ((widthMain * 33.33 / 100) < 300) {
                  minWidth = 33.33
            }
            if ((widthMain * 50 / 100) < 300) {
                  minWidth = 50
            }
            if ((widthMain * 100 / 100) < 300) {
                  minWidth = 100
            }

            if ((heightMain * 25 / 100) < 150) {
                  minHeight = 25
            }
            if ((heightMain * 33.33 / 100) < 150) {
                  minHeight = 33.33
            }
            if ((heightMain * 50 / 100) < 150) {
                  minHeight = 50
            }
            if ((heightMain * 100 / 100) < 150) {
                  minHeight = 100
            }

            let height = 100 / elms
            let width = 0
            if (elms === 0 || elms === 1) {
                  width = 100
                  height = 100
            } else if (elms === 2 && minWidth === 100) {
                  width = 50
                  height = 50
            } else if (elms === 2) {
                  width = 50
                  height = 100
            } else if (elms === 3 || elms === 4) {
                  width = 50
                  height = 50
            } else if (elms === 5 || elms === 6) {
                  width = 33.33
                  height = 50
            } else if (elms >= 7 && elms <= 9) {
                  width = 33.33
                  height = 33.33
            } else if (elms >= 10 && elms <= 12) {
                  width = 25
                  height = 33.33
            } else if (elms >= 13 && elms <= 16) {
                  width = 25
                  height = 25
            } else if (elms >= 17 && elms <= 20) {
                  width = 20
                  height = 25
            } else if (elms >= 21 && elms <= 25) {
                  width = 20
                  height = 20
            } else {
                  width = 100 / elms
            }
            let obtainWidth = minWidth >= width ? minWidth : width
            let obtainHeight = minHeight >= height ? minHeight : height
            let maxVictims = ~~(100 / obtainWidth) * ~~(100 / obtainHeight);
            let minVictims = ~~(100 / obtainWidth) * (~~(100 / obtainHeight) - 1);

            let videos = main.querySelectorAll("div.victim")
            let scrollOthers = document.getElementById('scrollOthers')
            for (let a = 0; a < videos.length; ++a) {
                  videos[a].style.minWidth = String(minWidth) + "%"
                  videos[a].style.minHeight = String(minHeight) + "%"
                  videos[a].style.setProperty("width", String(width) + "%")
                  videos[a].style.setProperty("height", String(height) + "%")
            }
            if (elms > maxVictims) {
                  for (let i = 0; i < elms - minVictims; i++) {
                        let indexId = Object.keys(connections)[Object.keys(connections).length - i - 1]
                        let tokenElm = main.querySelector(`[data-socket="${indexId}"]`)
                        console.log(Object.keys(connections), Object.keys(connections).length - i - 1, indexId);
                        if (tokenElm !== null) {
                              tokenElm.parentNode.removeChild(tokenElm)
                              scrollOthers.appendChild(tokenElm);
                              scrollOthers.style.setProperty("width", "100%")
                              scrollOthers.style.setProperty("height", String(obtainHeight / 2) + "%")
                              scrollOthers.querySelectorAll("div.victim").forEach(elm => { elm.style.height = "100%" })
                              main.style.setProperty("height", String(100 - (obtainHeight / 2)) + "%")
                        }
                  }
            } else {
                  scrollOthers.style.setProperty("height", "0%");
                  main.style.setProperty("height", "100%");
            }

            return { minWidth, minHeight, width, height, obtainWidth, obtainHeight, widthMain, heightMain }
      }

      createVictim = (stream, id, name, picture) => {
            // TODO mute button, full screen button
            var searchVidep = document.querySelector(`[data-socket="${id}"]`)
            if (searchVidep !== null) { // if i don't do this check it make an empyt square
                  searchVidep.srcObject = stream
            } else {
                  elms++;
                  let main = document.getElementById('main')
                  let scrollOthers = document.getElementById('scrollOthers')
                  let cssMesure = this.changeCssVideos(main)
                  let maxVictims = ~~(100 / cssMesure.width) * ~~(100 / cssMesure.height);
                  let minVictims = ~~(100 / cssMesure.width) * (~~(100 / cssMesure.height) - 1);
                  console.log(cssMesure, maxVictims, minVictims);

                  // <div className="victim inline-block border-8 border-transparent h-full w-full">
                  //       <div className="relative ring-2 rounded-lg overflow-hidden h-full w-full" style={{ backgroundColor: '#222' }}>
                  //             <div className="relative w-full h-full flex justify-center items-center">
                  //                   <div className={"bg-cover bg-no-repeat w-28 h-28 rounded-full"}
                  //                         style={{ backgroundImage: "url('" + this.state.userData.picture + "')" }}></div>
                  //             </div>
                  //             <video ref={this.localVideoref} disablePictureInPicture autoPlay muted className="absolute top-0 left-0 w-full h-full object-center" style={{ backgroundColor: '#222' }}></video>
                  //             <div className="absolute bottom-1 left-1 text-lg font-bold bg-black bg-opacity-20 px-2 rounded-sm">{this.state.userData.name}</div>
                  //       </div>
                  // </div>
                  let mainDiv = document.createElement('div')
                  let div = document.createElement('div')
                  let audioDiv = document.createElement('div')
                  let imgHolder = document.createElement('div')
                  let img = document.createElement('div')
                  let video = document.createElement('video')
                  let nameDiv = document.createElement('div')
                  mainDiv.className = "victim inline-block border-8 border-transparent"
                  div.className = "relative ring-2 h-full w-full rounded-lg overflow-hidden"
                  div.style.backgroundColor = '#222'
                  audioDiv.className = "absolute z-10 top-2 right-2 w-7 h-7 bg-blue-600 rounded-full flex justify-center items-center"
                  this.audioVisulaizer(stream, audioDiv);
                  imgHolder.className = "relative w-full h-full flex justify-center items-center"
                  img.className = "bg-cover bg-no-repeat w-28 h-28 rounded-full"
                  img.style.backgroundImage = "url('" + picture + "')"
                  video.disablePictureInPicture = true
                  video.className = "absolute top-0 left-0 w-full h-full object-center"
                  video.style.backgroundColor = '#222'
                  video.srcObject = stream
                  video.autoplay = true
                  video.playsinline = true
                  nameDiv.className = "absolute bottom-1 left-1 text-lg font-bold bg-black bg-opacity-20 px-2 rounded-sm"
                  nameDiv.innerText = name;
                  mainDiv.style.minHeight = String(cssMesure.minHeight) + "%"
                  mainDiv.style.minWidth = String(cssMesure.minWidth) + "%"
                  mainDiv.style.setProperty("width", String(cssMesure.width) + "%")
                  mainDiv.style.setProperty("height", String(cssMesure.height) + "%")
                  mainDiv.setAttribute('data-socket', id)
                  div.appendChild(audioDiv)
                  imgHolder.appendChild(img)
                  div.appendChild(imgHolder)
                  div.appendChild(video)
                  div.appendChild(nameDiv)
                  mainDiv.appendChild(div)

                  if (elms <= maxVictims) {
                        console.log("main input");
                        main.appendChild(mainDiv)
                  } else {
                        console.log("scroll input");
                        for (let i = 0; i < elms - minVictims; i++) {
                              let indexId = Object.keys(connections)[Object.keys(connections).length - i - 1]
                              let tokenElm = main.querySelector(`[data-socket="${indexId}"]`)
                              tokenElm?.parentNode.removeChild(tokenElm)
                              scrollOthers.appendChild(tokenElm);
                        }
                        scrollOthers.appendChild(mainDiv)
                  }
            }
      }

      refreshPeer = () => {
            for (let id in connections) {
                  connections[id].addStream(window.localStream)
                  // eslint-disable-next-line
                  connections[id].createOffer().then((description) => {
                        connections[id].setLocalDescription(description)
                              .then(() => {
                                    socket.emit('signal', id, JSON.stringify({ 'sdp': connections[id].localDescription }))
                              })
                              .catch(e => console.log(e))
                  })
            }
      }

      silence = () => {
            let ctx = new AudioContext()
            let oscillator = ctx.createOscillator()
            let dst = oscillator.connect(ctx.createMediaStreamDestination())
            oscillator.start()
            ctx.resume()
            return Object.assign(dst.stream.getAudioTracks()[0], { enabled: false })
      }
      black = ({ width = 640, height = 480 } = {}) => {
            let canvas = Object.assign(document.createElement("canvas"), { width, height })
            canvas.getContext('2d').fillRect(0, 0, width, height)
            let stream = canvas.captureStream()
            return Object.assign(stream.getVideoTracks()[0], { enabled: false })
      }

      gotMessageFromServer = (fromId, message) => {
            var signal = JSON.parse(message)
            console.log("signal");
            if (fromId !== socketId) {
                  if (signal.sdp) {
                        console.log("sdp");
                        connections[fromId].setRemoteDescription(new RTCSessionDescription(signal.sdp)).then(() => {
                              if (signal.sdp.type === 'offer') {
                                    connections[fromId].createAnswer().then((description) => {
                                          connections[fromId].setLocalDescription(description).then(() => {
                                                socket.emit('signal', fromId, JSON.stringify({ 'sdp': connections[fromId].localDescription }))
                                          }).catch(e => console.log(e))
                                    }).catch(e => console.log(e))
                              }
                        }).catch(e => console.log(e))
                  }

                  if (signal.ice) {
                        console.log("ice");
                        connections[fromId].addIceCandidate(new RTCIceCandidate(signal.ice)).catch(e => console.log(e))
                  }
            }
      }

      connectTotheSocketServer = () => {
            socket = io.connect(server_url, { secure: true, transport: ['websocket'] })

            socket.on('signal', this.gotMessageFromServer)

            socket.on('connect', () => {
                  // console.log(socket.id);
                  socket.emit('joinRoom', this.roomId, this.state.userId, this.state.userData, res => {
                        console.log(res);
                  });
                  socketId = socket.id

                  socket.on('user-left', (id) => {
                        delete connections[id]
                        let video = document.querySelector(`[data-socket="${id}"]`)
                        if (video !== null) {
                              elms--
                              video.parentNode.removeChild(video)

                              let main = document.getElementById('main')
                              this.changeCssVideos(main)
                        }
                  })

                  socket.on('userJoined', (id, roomData) => {
                        // console.log(socketId, roomData);
                        Object.keys(roomData).forEach(client => {
                              client = roomData[client]
                              connections[client.socketId] = new RTCPeerConnection(peerConnectionConfig)
                              victims[client.socketId] = {
                                    name: client.name,
                                    picture: client.picture
                              }
                              // Wait for their ice candidate
                              connections[client.socketId].onicecandidate = function (event) {
                                    if (event.candidate != null) {
                                          socket.emit('signal', client.socketId, JSON.stringify({ 'ice': event.candidate }))
                                    }
                              }
                              // Wait for their video stream
                              connections[client.socketId].onaddstream = (event) => {
                                    this.createVictim(event.stream, client.socketId, client.name, client.picture)
                              }
                              // Add the local video stream
                              if (window.localStream !== undefined && window.localStream !== null) {
                                    connections[client.socketId].addStream(window.localStream)
                              } else {
                                    let blackSilence = (...args) => new MediaStream([this.black(...args), this.silence()])
                                    window.localStream = blackSilence()
                                    connections[client.socketId].addStream(window.localStream)
                              }

                        });
                        if (id === socketId) {
                              for (let id2 in connections) {
                                    if (id2 === socketId) continue

                                    try {
                                          connections[id2].addStream(window.localStream)
                                    } catch (e) { }

                                    // eslint-disable-next-line
                                    connections[id2].createOffer().then((description) => {
                                          connections[id2].setLocalDescription(description)
                                                .then(() => {
                                                      socket.emit('signal', id2, JSON.stringify({ 'sdp': connections[id2].localDescription }))
                                                })
                                                .catch(e => console.log(e))
                                    })
                              }
                        }
                  });

                  socket.on('chat-message', (data, sender, id, time) => {
                        // console.log(`Message from : ${socketId === id ? "You" : sender}\nMessage : ${data}\nSocket Id : ${id}`);
                        this.setState({ msgArray: [...this.state.msgArray, { sender, message: data, id, time }] }, () => {
                              let holder = document.querySelector("div#msgHolder");
                              if (holder !== null) {
                                    holder.scrollTop = holder.scrollHeight;
                              }
                              console.log(this.state.msgArray);
                        });
                  });
                  this.sendMessage = (e) => {
                        if (this.state.message !== null && this.state.message !== undefined && this.state.message !== "") {
                              let today = new Date(),
                                    hour = today.getHours(),
                                    min = today.getMinutes(),
                                    meridian = "am";
                              if (hour > 12) {
                                    meridian = "pm"
                                    hour = hour - 12;
                              }
                              socket.emit('chat-message', this.state.message, this.state.userId, hour + ':' + min + meridian);
                              this.setState({ message: "" });
                              document.getElementById("msgInput").focus();
                        }
                  }
            })
      }

      cutCall = async () => {
            for (let id in connections) {
                  connections[id].close();
            }
            window.localStream = null;
            // this.props.history.push("/");
            window.location.reload();
      }

      setUpTest = async (type) => {
            if ((type === 'video' || type === 'menu') && this.state.video === true) {
                  this.localVideoTestref.current.srcObject = window.localStream
            }
      }

      tick = (e) => {
            const today = new Date(),
                  time = today.getHours() + ':' + today.getMinutes() + ':' + today.getSeconds();
            this.setState({ time })
      }

      componentDidMount() {
            this.intervalID = setInterval(
                  () => this.tick(),
                  1000
            );
            fetch('/gAuth-cookie', {
                  method: "POST",
                  headers: { 'content-type': 'application/json' }
            }).then(res => {
                  res.json().then(data => {
                        if (!data.isAuth) {
                              this.props.history.push("/");
                        } else {
                              this.getPermissions();
                              this.setState({
                                    userId: data.userId,
                                    userData: {
                                          name: data.username,
                                          picture: data.picture
                                    }
                              })
                        }
                  })
            })
      }

      componentWillUnmount() {
            clearInterval(this.intervalID);
            if (window.localStream) {
                  window.localStream.getTracks().forEach(track => {
                        track.stop();
                  })
            }
            this.setState(null)
      }

      isChrome = function () {
            let userAgent = (navigator && (navigator.userAgent || '')).toLowerCase()
            let vendor = (navigator && (navigator.vendor || '')).toLowerCase()
            let matchChrome = /google inc/.test(vendor) ? userAgent.match(/(?:chrome|crios)\/(\d+)/) : null
            return matchChrome !== null
      }

      render() {

            if (!this.isChrome()) {
                  return (
                        <div className="w-full h-full flex justify-center items-center text-center">
                              <div className="text-3xl font-extrabold">Sorry! This Only Works With Chrome Browser.</div>
                        </div>
                  )
            }

            if (this.state.conformation) {
                  return (
                        <div>
                              <p className="text-2xl font-extrabold p-3">Join Room: {this.roomId}</p>
                              <div className="max-w-md mx-auto ring-2 my-3 relative rounded-lg" style={{ backgroundColor: '#35363a', height: '260px' }}>
                                    <div className="absolute z-10 top-2 right-2 w-7 h-7 bg-blue-600 rounded-full"></div>
                                    <div className="relative w-full h-full">
                                          <div className={"bg-cover bg-no-repeat w-28 h-28 rounded-full absolute top-50% left-50% -ml-14 -mt-14"}
                                                style={{ backgroundImage: "url('" + this.state.userData.picture + "')" }}></div>
                                    </div>
                                    <video ref={this.localVideoref} disablePictureInPicture autoPlay muted className="absolute top-0 left-0 w-full h-full object-center"></video>
                                    <div className="absolute bottom-1 left-1 text-lg font-bold bg-black bg-opacity-20 px-2 rounded-md">{this.state.userData.name}</div>
                              </div>
                              <div className="flex w-full flex-wrap justify-center">
                                    <button onClick={this.handleAudio} disabled={window.localStream ? false : true} className="inline-block p-2 bg-red-500 hover:bg-red-400 rounded-full cursor-pointer m-1 disabled:opacity-50">{this.state.audio ? <MicOn /> : <MicOff />}</button>
                                    <button onClick={this.handleVideo} disabled={window.localStream ? false : true} className="inline-block p-2 bg-red-500 hover:bg-red-400 rounded-full cursor-pointer m-1 disabled:opacity-50">{this.state.video ? <VideoOn /> : <VideoOff />}</button>
                                    <button onClick={e => { this.setState({ conformation: false }, async () => { await this.init(); }) }} disabled={window.localStream ? false : true} className="inline-block py-2 px-4 bg-red-500 hover:bg-red-400 disabled:opacity-50 rounded-full cursor-pointer m-1">Join</button>
                              </div>
                        </div>
                  )
            }

            return (
                  <div className="h-full w-full relative">

                        <div className={"align-top relative " + (this.state.messages ? "inline-block" : "block")} style={{ width: this.state.messages ? "calc(100% - 300px)" : "100%", height: "calc(100% - 50px)" }}>
                              <div id="main" className={"main h-full w-full relative p-2 justify-center flex flex-wrap "}>
                                    <ResizeSensor onResize={(w, h) => this.changeCssVideos(document.getElementById('main'))} />

                                    <div className="victim inline-block border-8 border-transparent h-full w-full">
                                          <div className="relative ring-2 rounded-lg overflow-hidden h-full w-full" style={{ backgroundColor: '#222' }}>
                                                <div className="relative w-full h-full flex justify-center items-center">
                                                      <div className={"bg-cover bg-no-repeat w-28 h-28 rounded-full"}
                                                            style={{ backgroundImage: "url('" + this.state.userData.picture + "')" }}></div>
                                                </div>
                                                <video ref={this.localVideoref} disablePictureInPicture autoPlay muted className="absolute top-0 left-0 w-full h-full object-center" style={{ backgroundColor: '#222' }}></video>
                                                <div className="absolute bottom-1 left-1 text-lg font-bold bg-black bg-opacity-20 px-2 rounded-md">{this.state.userData.name}</div>
                                          </div>
                                    </div>

                              </div>
                              <div id="scrollOthers" className="wrapperX flex flex-nowrap">
                              </div>
                        </div>

                        {this.state.messages ?
                              <div className="text-black inline-block absolute top-0 left-0 bg-white align-top w-full 600:rounded-lg 600:w-300px 600:relative" style={{ height: "calc(100% - 50px)" }}>
                                    <div className="relative h-full">
                                          <p className="text-2xl p-1 font-extrabold border-b-2">In Call Messages</p>
                                          <p onClick={e => { this.setState({ messages: false }) }} className="absolute right-1 top-0 text-3xl cursor-pointer" style={{ height: "42px" }}>⨯</p>
                                          <div id="msgHolder" className="wrapperY-lite" style={{ height: "calc(100% - 82px)" }}>
                                                {this.state.msgArray.map((item, index) => (
                                                      <div key={index} className="relative m-2 p-1 rounded-lg bg-gray-300 break-words">
                                                            <p className="text-lg font-bold">{socketId === item.id ? "You" : item.sender}</p>
                                                            <p className="text-xs font-semibold text-gray-800 absolute top-1 right-1">{item.time}</p>
                                                            <p className="text-sm font-semibold">{item.message}</p>
                                                      </div>
                                                ))}
                                          </div>
                                          <div className="w-full h-40px p-1">
                                                <input value={this.state.message}
                                                      id="msgInput"
                                                      onChange={e => { this.setState({ message: e.target.value }) }}
                                                      type="text"
                                                      placeholder="Your Message..."
                                                      className="h-full rounded-lg bg-gray-300 placeholder-gray-700 px-2 align-top"
                                                      style={{ width: 'calc(100% - 32px)' }}
                                                      onKeyDown={e => { if (e.keyCode === 13) this.sendMessage() }}
                                                />
                                                <div className="inline-block w-32px h-full cursor-pointer" onClick={this.sendMessage} ><Send height="100%" width="100%" fill="rgba(55, 65, 81, 1)" /></div>
                                          </div>
                                    </div>
                              </div>
                              : null
                        }

                        <div className="Control wrapperX relative h-50px">
                              <div className="hidden pl-1 h-full align-top 580:block text-lg font-bold">
                                    {this.state.time} | {this.roomId}
                              </div>
                              <div className="block h-full w-192px align-top 580:absolute top-0 580:right-50% 580:-mr-96px py-5px">
                                    <button onClick={this.handleAudio} disabled={window.localStream ? false : true} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1 disabled:opacity-50">{this.state.audio ? <MicOn /> : <MicOff />}</button>
                                    <button onClick={this.cutCall} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1"><Power /></button>
                                    <button onClick={this.handleVideo} disabled={window.localStream ? false : true} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1 disabled:opacity-50">{this.state.video ? <VideoOn /> : <VideoOff />}</button>
                                    {this.state.screenAvailable && <button onClick={this.screenShare} disabled={window.localStream ? false : true} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1 disabled:opacity-50">{!this.state.screen ? <ScreenShare /> : <StopScreenShare />}</button>}
                                    {/* <button onClick={e => { this.createVictim(window.localStream, this.state.time) }} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1 disabled:opacity-50">Add</button> */}
                              </div>
                              <div className="block h-full align-top absolute right-1 top-0 py-5px">
                                    <div onClick={e => { this.setState({ messages: !this.state.messages }) }} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1"><MsgBox /></div>
                                    <div onClick={e => { this.setState({ popup: true }, () => { this.setUpTest(this.state.popupScreen) }) }} className="inline-block p-2 bg-btn hover:bg-red-500 rounded-full cursor-pointer mx-1"><Settings /></div>
                              </div>
                        </div>

                        {this.state.popup &&
                              <div className="Popup">
                                    <div onClick={e => { this.setState({ popup: false }) }} className="Overlay absolute top-0 w-full h-full z-10 cursor-pointer bg-black bg-opacity-50 flex justify-center items-center">
                                          <div onClick={e => { e.stopPropagation(); }} style={{ maxWidth: "500px", maxHeight: "700px" }} className="relative z-20 p-2 bg-white text-black shadow-2xl ring-gray-400 hover:ring-4 h-full w-full sssm:h-70% sssm:w-60% sssm:rounded-xl sssm:animate-popup">
                                                <div className="border-b-2 border-gray-700 relative p-1">
                                                      <p className="text-2xl font-extrabold">Settings</p>
                                                      <p onClick={e => { this.setState({ popup: false }) }} className="absolute right-1 top-0 text-3xl h-full">⨯</p>
                                                </div>

                                                <div className="w-full relative" style={{ height: 'calc(100% - 42px)' }}>
                                                      <div className="w-full h-full block align-top sssm:inline-block sssm:w-30% sssm:border-r-2 border-gray-700">
                                                            <div onClick={e => { this.setState({ popupScreen: 'audio' }) }} className={"text-lg " + (this.state.popupScreen === 'audio' ? "bg-gray-300" : "bg-gray-200") + " align-middle pl-1"}>Audio</div>
                                                            <div onClick={e => { this.setState({ popupScreen: 'video' }, () => { this.setUpTest('video') }) }} className={"text-lg " + (this.state.popupScreen === 'video' ? "bg-gray-300" : "bg-gray-200") + " align-middle pl-1"}>Video</div>
                                                      </div>
                                                      <div className={"wrapper w-full h-full " + (this.state.popupScreen === 'menu' ? 'hidden' : 'block') + " align-top absolute top-0 bg-white sssm:inline-block sssm:relative sssm:w-70% p-1"}>
                                                            <div className="block sssm:hidden" onClick={e => { this.setState({ popupScreen: 'menu' }) }}><ArrowBack /></div>
                                                            {this.state.popupScreen === 'audio' ?
                                                                  <div>
                                                                        {this.state.audio ?
                                                                              <AudioAnalyser className="w-full h-28 border-2 border-black rounded-lg" />
                                                                              : null
                                                                        }
                                                                        <label htmlFor="audioIn" className="font-bold">Select Audio Source:</label>
                                                                        <select onChange={this.handleChange} name="audioIn" className="border-2 border-black rounded-md p-2 w-full">
                                                                              {this.state.audioInDevices.map((item, index) => (
                                                                                    <option value={item.deviceId} key={index}>{item.label}</option>
                                                                              ))}
                                                                        </select>
                                                                        <label htmlFor="audioOut" className="font-bold">Select Speaker Source:</label>
                                                                        <div onClick={e => { this.localAudioTestref.current.play() }} className="inline-block align-top px-1"><Speaker /></div>
                                                                        <audio ref={this.localAudioTestref} controls className="hidden"><source src={testAudio} type="audio/mp3" /></audio>
                                                                        <select onChange={this.handleChange} disabled={!('sinkId' in HTMLMediaElement.prototype)} name="audioOut" className="border-2 border-black rounded-md p-2 w-full">
                                                                              {this.state.audioOutDevices.map((item, index) => (
                                                                                    <option value={item.deviceId} key={index}>{item.label}</option>
                                                                              ))}
                                                                        </select>
                                                                  </div>
                                                                  :
                                                                  <div>
                                                                        <div className="w-full h-28 p-2">
                                                                              {!this.state.video ?
                                                                                    <div className="border-2 border-black rounded-lg w-full h-full flex justify-center items-center font-bold text-base">Your Vodeo Is Off</div>
                                                                                    :
                                                                                    <video ref={this.localVideoTestref} disablePictureInPicture autoPlay muted className="w-full h-full object-center rounded-lg"></video>
                                                                              }
                                                                        </div>
                                                                        <label htmlFor="video" className="font-bold">Select Video Source:</label>
                                                                        <select onChange={this.handleChange} defaultValue={this.state.currentVideoDevice} name="video" className="border-2 border-black rounded-md p-2 w-full">
                                                                              {this.state.videoDevices.map((item, index) => (
                                                                                    <option value={item.deviceId} key={index}>{item.label}</option>
                                                                              ))}

                                                                        </select>
                                                                  </div>
                                                            }
                                                      </div>
                                                </div>
                                          </div>
                                    </div>
                              </div>
                        }
                  </div>
            )
      }
}
export default withRouter(Video);
