import { useState, useEffect } from 'react';
import { GoogleLogin, GoogleLogout } from 'react-google-login';

// icons
import { ReactComponent as GLogo } from '../assets/images/svg/Google_Logo.svg';

export default function Home(props) {
      // eslint-disable-next-line
      const [keepSignedIn, setKeepSignedIn] = useState(false);
      const [auth, setAuth] = useState(undefined);

      const responseGoogle = async (response) => {
            let { tokenId } = response;
            await fetch('/gAuth', {
                  method: "POST",
                  headers: {
                        'content-type': 'application/json'
                  },
                  body: JSON.stringify({
                        token: tokenId
                  })
            }).then(res => {
                  res.json().then(data => {
                        if (res.status === 200) {
                              console.log("Successfully Authonticated.");
                        }
                        if (res.status === 201) {
                              console.log("Successfully Registered");
                        }
                        if (res.status === 500) {
                              console.error("Failed to Authonticate");
                        }
                        getAuth();
                  })
            })
      }
      
      const logOut = async () => {
            fetch('/logout', {
                  method: "POST",
                  headers: { 'content-type': 'application/json' }
            }).then(res => {
                  res.json().then(data => {
                        if (res.status === 200) {
                              console.log('Successfully logged out');
                              setAuth(false);
                        }
                  })
            })
      }

      function getAuth() {
            fetch('/gAuth-cookie', {
                  method: "POST",
                  headers: { 'content-type': 'application/json' }
            }).then(res => {
                  res.json().then(data => {
                        if (!data.isAuth) {
                              setAuth(false);
                        } else {
                              setAuth(true);
                        }
                  })
            })
      }

      useEffect(e => {
            if (window.localStream) {
                  window.localStream.getTracks().forEach(track => {
                        track.stop();
                  })
            }
            getAuth();
      }, []);

      return (
            <div className="h-full">
                  <div className="text-center font-extrabold text-4xl px-5">Welcome To The ChatCord Video Meeting</div>
                  {auth === false ?
                        <>
                              <div className="font-bold p-6 text-2xl">Login to continue ...</div>
                              <GoogleLogin
                                    clientId="238100528248-vhcd4fi9uhgas0b0ckl3moep9tma3hjc.apps.googleusercontent.com"
                                    render={renderProps => (
                                          <button onClick={renderProps.onClick} disabled={renderProps.disabled} style={{ width: '184px', height: '42px', backgroundColor: '#4285f4', borderRadius: '2px', boxShadow: '0 3px 4px 0 rgba(0,0,0,.25)' }} className="relative ml-7 disabled:opacity-50">
                                                <div style={{ position: 'absolute', top: 0, left: 0, marginTop: '2px', marginLeft: '2px', width: '38px', height: '38px', borderRadius: '2px', backgroundColor: '#fff' }}>
                                                      <GLogo style={{ position: 'absolute', marginTop: '9px', marginLeft: '9px', width: '18px', height: '18px' }} />
                                                </div>
                                                <p className="font-semibold" style={{ float: 'right', color: '#fff', fontSize: '15px', letterSpacing: '0.2px', fontFamily: '"Roboto"', width: "142px" }}>Sign in with Google</p>
                                          </button>
                                    )}
                                    buttonText="Login"
                                    onSuccess={responseGoogle}
                                    onFailure={responseGoogle}
                                    cookiePolicy={'single_host_origin'}
                                    isSignedIn={keepSignedIn}
                              />
                              <div className="ml-7 mt-3">
                                    <p className="inline-block align-top pr-2">Keep me signed in : </p>
                                    <input className="inline-block align-middle cursor-pointer w-5 h-5" type="checkbox" checked={keepSignedIn} onChange={e => { setKeepSignedIn(!keepSignedIn) }} />
                              </div>
                        </>
                        : auth === true ?
                              <>
                                    <GoogleLogout
                                          clientId="238100528248-vhcd4fi9uhgas0b0ckl3moep9tma3hjc.apps.googleusercontent.com"
                                          render={renderProps => (
                                                <button onClick={renderProps.onClick} disabled={renderProps.disabled} style={{ width: '184px', height: '42px', backgroundColor: '#4285f4', borderRadius: '2px', boxShadow: '0 3px 4px 0 rgba(0,0,0,.25)' }} className="relative ml-7 disabled:opacity-50">
                                                      <div style={{ position: 'absolute', top: 0, left: 0, marginTop: '2px', marginLeft: '2px', width: '38px', height: '38px', borderRadius: '2px', backgroundColor: '#fff' }}>
                                                            <GLogo style={{ position: 'absolute', marginTop: '9px', marginLeft: '9px', width: '18px', height: '18px' }} />
                                                      </div>
                                                      <p className="font-semibold" style={{ float: 'right', color: '#fff', fontSize: '15px', letterSpacing: '0.2px', fontFamily: '"Roboto"', width: "142px" }}>Log Out</p>
                                                </button>
                                          )}
                                          buttonText="Logout"
                                          onLogoutSuccess={logOut}
                                    />
                              </>
                              : 
                              <>
                                    <div></div>
                              </>
                  }
            </div>
      );
}