import { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';

import Home from './views/home';
import Video from './views/comps/video';


function App() {

      return (
            <Router>
                  <Switch>
                        <Route exact path="/" component={Home} />
                        {/* Protected Routes Not Working Properly Right Now */}
                        <ProtectedRoutes exact path="/p">Protection</ProtectedRoutes>
                        <Route exact path={["/room/:url"]} render={Video}></Route>
                        <Redirect to="/" />
                  </Switch>
            </Router>
      );
}

// Later to be fixed.
class ProtectedRoutes extends Component {
      constructor(props) {
            super(props);
            this.state = {
                  isAuth : true
            }
      }
      componentDidMount = async () => {
            await fetch('/gAuth-cookie', {
                  method: "POST",
                  headers: { 'content-type': 'application/json' }
            }).then(res => {
                  res.json().then(data => {
                        this.setState({ isAuth : data.isAuth });
                  })
            })
      }
      render() {
            const { children, ...props } = this.props;
            return (
                  <Route {...props} render={({ location }) => {
                        console.log(this.isAuth);
                        return this.state.isAuth ? (
                              children
                        ) : (
                              <Redirect to={{ pathname: '/', state: { from: location } }} />
                        )
                  }}></Route>
            )
      }
}

export default App;
